 // Content //////////////////////////////////
 
 .title {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

$border-style: 1px solid #cbcbcb;

// Table
  .table {
    border-collapse: collapse;
    border-spacing: 0;
    empty-cells: show;
    border: $border-style;

    td, th {
      border-left: $border-style;
      border-width: 0 0 0 1px;
      font-size: inherit;
      margin: 0;
      overflow: visible;
      padding: 0.5rem 1rem;
    }

    thead {
      background-color: #e0e0e0;
      color: #000;
      text-align: left;
      vertical-align: bottom;
    }
  }

 // End Content //////////////////////////////////





