h1{
  color: gray;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after { 
  clear: both;
}

.clearfix {
  *zoom: 1;
} 
@mixin bg_img($uri) {
    background-image: url($uri);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
}

#wrapper {}

@import 'header'; 
@import 'content';
@import 'footer'; 



